<template>
  <li class="hot-item">
    <div class="icon" :style="{ backgroundImage: `url(${song.picUrl})` }">
      <div class="play" @click="togglePlay" v-if="paused">
        <img src="../assets/images/itemplay.png" />
      </div>
      <div
        class="pause"
        @click="togglePlay"
        v-else-if="currentmusic && currentmusic.id === song.id"
      >
        <img src="../assets/images/itempause.png" />
      </div>
      <div class="play" @click="togglePlay" v-else>
        <img src="../assets/images/itemplay.png" />
      </div>
    </div>
    <div class="info">
      <div class="name">{{ song.name }}</div>
      <div class="author">
        <van-tag plain type="primary" color="#FF7C24">HQ</van-tag>
        <span
          class="auname"
          v-for="(artist, index) in song.song.artists"
          :key="index"
        >
          <template v-if="index"> / </template>{{ artist.name }}
        </span>
      </div>
    </div>
    <div class="more"><img src="../assets/images/inactivestar.png"></div>
  </li>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "HotItem",
  props: ["song","index"],
  computed: {
    ...mapState(["currentmusic", "paused"]),
  },
  methods: {
    togglePlay() {
      let song = this.song;
      let index = this.index;

      this.$store.commit("updateCurrentmusic", song);
      this.$store.commit("updateCurrentindex", index);
      this.$emit('update:playlist')
    },
  }
};
</script>

<style lang="less" scoped>
li.hot-item {
  padding: 10px 0;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
  .icon {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-size: 100%;
    position: relative;
    div {
      position: absolute;
      top: 50%;
      left: 50%;
      img {
        width: 100%;
      }
    }
    .play {
      transform: translateY(-44%) translateX(-38%);
    }
    .pause {
      transform: translateY(-44%) translateX(-50%);
    }
  }
  .info {
    // flex-basis: 65%;
    flex: 1;
    margin-left: 15px;
    line-height: 28px;
    .name {
      flex-basis: 100%;
      font-size: 14px;
      color: #232323;

      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1; /*行数*/
      -webkit-box-orient: vertical;
    }
    .author {
      flex-basis: 100%;
      font-size: 13px;
      color: #999;

      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1; /*行数*/
      -webkit-box-orient: vertical;
      
      /deep/ .van-tag{
        font-size: 1px;
      }
      span {
        margin: 0 2px;
      }
      .auname {
        display: inline-block;
      }
    }
  }
  .more {
    width: 30px;
    height: 30px;
    margin-right: 30px;
    margin-left: 10px;
    // .van-icon {
    //   font-size: 25px;
    // }
    
  }
}
</style>