<template>
  <div class="mv-player">
    <van-popup
      v-model="mvShow"
      :close-on-click-overlay="false"
      :style="{ height: '50%', width: '100%' }"
    >
      <div class="close" @click="close">
        <van-icon name="cross" color="#fff" />
      </div>
      <video :src="src" controls autoplay ref="video"></video>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "MvPlayer",
  props: ["mvShow", "mvId"],
  data: function () {
    return {
      src: "",
    };
  },
  watch: {
    mvId() {
      this.axios
        .get(`/mv/url?id=${this.mvId}`)
        .then((response) => {
          this.src = response.data.data.url;
        })
        .catch((err) => {});
    },
  },
  methods: {
    close() {
      this.$refs.video.pause();
      this.$emit("update:mvShow", false);
    },
  },
};
</script>

<style lang="less" scoped>
.mv-player {
  /deep/ .van-popup {
    display: flex;
    justify-content: center;
    background-color: #000;
  }
  .close {
    position: fixed;
    top: 5%;
    right: 5%;
    z-index: 10000;
    .van-icon {
      font-size: 25px;
    }
  }
}
</style>