<template>
  <div class="recommend">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item>
        <img
          src="https://y.gtimg.cn/music/common/upload/MUSIC_FOCUS/3368061.jpg?max_age=2592000"
        />
      </van-swipe-item>
      <van-swipe-item>
        <img
          src="https://y.gtimg.cn/music/common/upload/MUSIC_FOCUS/3354157.jpg?max_age=2592000"
        />
      </van-swipe-item>
      <van-swipe-item>
        <img
          src="https://y.gtimg.cn/music/common/upload/MUSIC_FOCUS/3366585.jpg?max_age=2592000"
        />
      </van-swipe-item>
      <van-swipe-item>
        <img
          src="https://y.gtimg.cn/music/common/upload/MUSIC_FOCUS/3363432.jpg?max_age=2592000"
        />
      </van-swipe-item>
    </van-swipe>

    <div class="mv">
      <MainTitle>MV推荐</MainTitle>
      <ul>
        <MvItem
          v-for="mv in limitMvs"
          :key="mv.id"
          :mv="mv"
          @play-mv="playMv"
        ></MvItem>
      </ul>

      <template v-if="limitMvs.length <= 4">
        <div @click="allmv"><CheckAll></CheckAll></div>
      </template>
      <template v-else>
        <div @click="recovermv"><RecoverAll></RecoverAll></div>
      </template>
      <MvPlayer
        :mvShow="mvShow"
        :mvId="mvId"
        @update:mvShow="mvShow = $event"
      ></MvPlayer>
    </div>

    <div class="hot">
      <MainTitle>最新音乐</MainTitle>
      <ul>
        <HotItem
          v-for="(song, index) in hotSongs"
          :key="song.id"
          :song="song"
          :index="index"
          @update:playlist="updatePlayList(hotSongs)"
        ></HotItem>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import MainTitle from "@/components/MainTitle.vue";
import HotItem from "@/components/HotItem.vue";
import MvItem from "@/components/MvItem.vue";
import CheckAll from "@/components/CheckAll.vue";
import RecoverAll from "@/components/RecoverAll.vue";
import MvPlayer from "@/components/MvPlayer.vue";

export default {
  name: "Recommend",
  components: {
    MainTitle,
    HotItem,
    MvItem,
    CheckAll,
    RecoverAll,
    MvPlayer,
  },
  computed: {
    ...mapState(["currentmusic", "paused"]),
  },
  data: function () {
    return {
      hotSongs: [],
      mvs: [],
      limitMvs: [],
      count: 0,
      mvShow: false,
      mvId: 0,
    };
  },
  created() {
    console.log("recommend----created");
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      duration: 0,
    });

    this.axios
      .get("/personalized/newsong")
      .then((response) => {
        this.hotSongs = response.data.result;
        this.count = this.count + 1;
        if (this.count >= 2) {
          this.$toast.clear();
        }
      })
      .catch((err) => {
        console.log(err);
        this.$toast.clear();
      });

    this.axios
      .get("/mv/exclusive/rcmd", { params: { limit: 10 } })
      .then((response) => {
        this.mvs = response.data.data;
        this.limitMvs = this.mvs.slice(0, 4);
        this.count = this.count + 1;
        if (this.count >= 2) {
          this.$toast.clear();
        }
      })
      .catch((err) => {
        console.log(err);
        this.$toast.clear();
      });
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      // console.log('beforeRouteEnter');
      vm.$store.commit("updateActiveid", 0);
    });
  },
  methods: {
    playMv(id) {
      this.mvId = id;
      this.mvShow = true;
      this.$emit("change-pause");
    },
    allmv() {
      this.limitMvs = this.mvs;
    },
    recovermv() {
      this.limitMvs = this.mvs.slice(0, 4);
    },
    updatePlayList(list) {
      this.$store.commit("updatePlaylist", list);
    },
  },
};
</script>

<style lang="less" scoped>
.recommend {
  margin-top: 86px;

  .my-swipe .van-swipe-item {
    img {
      width: 100%;
    }
  }
  .hot,
  .mv {
    background-color: #fff;
    font-size: 14px;
    margin-top: 4px;
  }
  .hot {
    padding: 5px 0 50px 15px;
  }
  .mv {
    padding: 5px 0 15px 15px;
    ul {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 20px;
    }
  }
}
</style>